@import-normalize;

html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  padding:0;
  margin:0;
  min-width: 320px;
  font-family: "Roboto, Arial sans-serif";
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  color: #000000;
  background: #E5E5E5;
}